function initSlider(slider, options = false, unslick = false, $ = jQuery){

	if(unslick){
		$(slider).filter('.slick-initialized').slick('unslick');
		return;
	}

	let args = {
		arrows: false,
		dots: true,
		slidesToShow: 1,
		slidesToScroll: 1,
		adaptiveHeight: true
	}

	if(options){
		for(let key in options){
			args[key] = options[key];
		}
	}

	$(slider).not('.slick-initialized').slick(args);
}

